import React from 'react';
import PropTypes from 'prop-types';
import { Panel, PanelHeader, HeaderButton, Gallery, Button, FixedLayout, Div, Header, Group, platform, IOS } from '@vkontakte/vkui';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';

class Task extends React.Component {
	constructor(props) {
		super(props);

		this.state = {
            slideIndex: 0,
            imageHeight : 350
        }
	}

	render() {
		let { id, task, go } = this.props
		const osname = platform();
		return (
			<Panel id={id}>
				<PanelHeader
					left={<HeaderButton onClick={go} data-to="tasks">
						{osname === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
					</HeaderButton>}
				>
					Задание
				</PanelHeader>
				<Header>{task.name} <b>{task.price} баллов</b></Header>
				{ 
                        task.photos.length > 0 && 
                        (
                            <Gallery
                                slideWidth="100%"
                                style={{ height: this.state.imageHeight, textAlign : 'center' }}
                                bullets="dark"
                            >
                                { 
                                    task.photos.map((photo, index) => (
                                        <div key={index} style={{width: '100%', height : '100%', backgroundColor : '#fff'}}>
                                            <img 
                                                alt=""
                                                style={{  
                                                    maxHeight : this.state.imageHeight
                                                }} src={photo}>
                                            </img>
                                        </div>
                                    ))
                                }
                            </Gallery>
                        )
                    }
                    {
                        task.photos.length === 0 &&
                        (
                            <Gallery
                                slideWidth="100%"
                                style={{ height: this.state.imageHeight, textAlign : 'center' }}
                            >
                                <div style={{width: '100%', height : '100%', backgroundColor : '#fff'}}>
                                    <img 
                                        alt=""
                                        style={{  
                                            maxHeight : '350px'
                                        }} src="https://vk.com/images/camera_200.png"></img>
                                </div>
                            </Gallery>
                        )
					}
                    <Group>
                        <Div>
                            {task.description}
                        </Div>
                    </Group>
                    <FixedLayout vertical="bottom">
                        <Div>
                            <Button size="xl" level="commerce">Я выполнил</Button>
                        </Div>
                    </FixedLayout>
			</Panel>
		);
	}
}

export default Task;