import React from 'react';
import connect from '@vkontakte/vkui-connect';
import { View, Epic, Tabbar, TabbarItem, Panel, PanelHeader } from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';

import Icon24Home from '@vkontakte/icons/dist/24/home';
import Icon24Similar from '@vkontakte/icons/dist/24/similar';
import Icon24Favorite from '@vkontakte/icons/dist/24/favorite';

import Home from './panels/Home';
import Top from './panels/Top';
import Tasks from './panels/Tasks';
import Task from './panels/Task';

class App extends React.Component {
	constructor(props) {
		super(props);

		this.state = {
			activeStory: 'index',
			http_web_site: 'https://pso31.ru/active_pso/vkapp/',
			topDataTopPlace: '0',
			tasksCount: '0',
			userData: null,
			topData: null,
			tasksData: null,
			photoState: true,
			dataState: true,
			topState: true,
			tasksState: true,
			fetchedUser: null,
		};
		this.onStoryChange = this.onStoryChange.bind(this);
	}

	onStoryChange (e) {
		this.setState({ activeStory: e.currentTarget.dataset.story })
	  }	

	componentDidMount() {
		connect.subscribe((e) => {
			switch (e.detail.type) {
				case 'VKWebAppGetUserInfoResult':
					this.setState({ fetchedUser: e.detail.data });

					// Получаем объект из таблицы

					fetch(this.state.http_web_site + "/api/method/users.Get.php?user_id=" + this.state.fetchedUser.id)
					.then((res) => res.json())
					.then((data) => (
						this.setState({ userData: data.response }),
						this.setState({ dataState: false })
					))

					//Получаем у каждого фотографию и заносим в таблицу

					fetch(this.state.http_web_site + "/api/method/users.setPhoto.php?user_id=" + this.state.fetchedUser.id + "&photo=" + this.state.fetchedUser.photo_200)
					.then((res) => res.json())
					.then((data) => (
						this.setState({ photoState: false }),
						console.log(data.response)
					))

					//Получаем общий ТОП

					fetch(this.state.http_web_site + "/api/method/app.getTop.php?limit=50&user_id=" + this.state.fetchedUser.id)
					.then((res) => res.json())
					.then((data) => (
						this.setState({ topState: false }),
						this.setState({ topData: data.response }),
						this.setState({ topDataTopPlace: data.top_place }),
						console.log(data)
					))
					break;
				default:
					console.log(e.detail.type);
			}
		});
		connect.send('VKWebAppGetUserInfo', {});
	}

	getInfoTask(id) {
		fetch(this.state.http_web_site + "/api/method/tasks.Get.php?task_id =" + id)
		.then((res) => res.json())
		.then((data) => (
			this.setState({ taskData: data.response }),
			console.log(data)
		))
	}

	getTasks() {
		//Получаем задания

		fetch(this.state.http_web_site + "/api/method/app.getTasks.php")
		.then((res) => res.json())
		.then((data) => (
			this.setState({ tasksState: false }),
			this.setState({ tasksData: data.response }),
			this.setState({ tasksCount: data.count }),
			console.log(data)
		))
	}

	go = (e, object) => {
		let activePanel = e.currentTarget.dataset.to
		this.setState({
			activePanel: activePanel
		})
		if (activePanel === 'tasks') {
			connect.send("VKWebAppSetLocation", {
				"location": "tasks"
			});
			this.getTasks()
			this.setState({ activeStory: "tasks" })
		}
		if (activePanel === 'task') {
			connect.send("VKWebAppSetLocation", {
				"location": `task${object.taskId}`
			});
			this.getInfoTask(object.taskId)
			this.setState({ activeStory: "tasks" })
		}
	}

	render() {
		return (
			<Epic activeStory={this.state.activeStory} tabbar={
				<Tabbar>
				  <TabbarItem
					onClick={this.onStoryChange}
					selected={this.state.activeStory === 'index'}
					data-story="index"
					text="Главная"
				  ><Icon24Home /></TabbarItem>
				  <TabbarItem
					onClick={this.onStoryChange}
					selected={this.state.activeStory === 'tasks'}
					data-story="tasks"
					label="0"
					text="Задания"
				  ><Icon24Similar /></TabbarItem>
				  <TabbarItem
					onClick={this.onStoryChange}
					selected={this.state.activeStory === 'top'}
					data-story="top"
					text="Топ"
				  ><Icon24Favorite /></TabbarItem>
				</Tabbar>
			  }>
				<View id="index" activePanel="home">
					<Home id="home" fetchedUser={this.state.fetchedUser} dataUser={this.state.userData} spinner={this.state.dataState} />
				</View>
				<View id="tasks" activePanel="tasks">
					<Tasks id="tasks" go={this.go} dataUser={this.state.userData} tasks={this.state.tasksData} spinner={this.state.tasksState} count={this.state.tasksCount} />
					<Task id="task" task={this.state.taskData} go={this.go}/>
				</View>
				<View id="top" activePanel="top">
					<Top id="top" dataUser={this.state.userData} top={this.state.topData} spinner={this.state.topState} place={this.state.topDataTopPlace} />
				</View>
			  </Epic>
		);
	}
}

export default App;
