import React from 'react';
import { Panel, Avatar, PanelHeader, List, Cell, Spinner, FixedLayout, Div } from '@vkontakte/vkui';
import './style.css';


const Top = ({ id, dataUser, top, spinner, place }) => (
	<Panel id={id}>
		<PanelHeader>Топ пользователей</PanelHeader>

        {spinner &&
		<div style={{display: 'flex', alignItems: 'center', flexDirection: 'colomn'}}>
			<Spinner size="large" style={{ marginTop: 20 }}/>
        </div>}
        
        <List>
            {top.length > 0 && top.map((item, index) => (
            <Cell
                data-top-id={item.id}
                indicator={index + 1}
                href={'//vk.com/id' + item.user_id}
                key={index}
                title="Король лайков"
                description={+item.p_likes + +item.p_flikes + +item.p_tlikes + +item.p_alllikes + +item.p_comm + +item.p_gposts + +item.p_gcomm + " баллов"}
                before={item.photo ? <Avatar src={item.photo}/> : <Avatar src='https://vk.com/images/camera_100.png'/>}>
                {item.username ? item.username : 'Unknown'}
            </Cell>))}
        
            <FixedLayout vertical="bottom">
                <Cell
                    className="fixed-top-account"
                    description={+dataUser.p_likes + +dataUser.p_flikes + +dataUser.p_tlikes + +dataUser.p_alllikes + +dataUser.p_comm + +dataUser.p_gposts + +dataUser.p_gcomm + " баллов"}
                    indicator={"~" + place}
                    key={top.top_place}
                    before={dataUser.photo ? <Avatar src={dataUser.photo}/> : <Avatar src='https://vk.com/images/camera_100.png'/>}>
                    {dataUser.username ? dataUser.username : 'Unknown'}
                </Cell>
            </FixedLayout>
        </List>
	</Panel>
);

export default Top;