import React from 'react';
import { Panel, Avatar, PanelHeader, List, Cell, Spinner, Group, Div } from '@vkontakte/vkui';
import './style.css';


class Tasks extends React.Component 
{
    render() 
    {
		let { 
			id, tasks, dataUser, spinner, count
		} = this.props

        return (
                <Panel id={id}>
                    <PanelHeader>Задания</PanelHeader>

                    {spinner &&
                    <div style={{display: 'flex', alignItems: 'center', flexDirection: 'colomn'}}>
                        <Spinner size="large" style={{ marginTop: 20 }}/>
                    </div>}
                    <List>
                    <Group title="Первостепенные задания" description="Задания первого класса, наиболее важные">
                    {
                        tasks.first.length === 0 &&
                        <Cell>Пусто</Cell>
                    }
                    {
                        tasks.first.length > 0 && tasks.first.map((item, index) => (
                            <Cell
                                data-top-id={item.id}
                                onClick={(e) => this.props.go(e, { taskId : item.id })}
                                key={index}
                                title="Задание"
                                before={item.picture ? <Avatar src={item.picture}/> : <Avatar src='https://vk.com/images/camera_100.png'/>}
                            >
                                {item.name ? item.name : 'Unsigned'}
                            </Cell>
                            ))
                    }
                    </Group>

                    <Group title="Второстепенные задания" description="Второстепенные задания">
                    {
                        tasks.second.length === 0 &&
                        <Cell>Пусто</Cell>
                    }
                    {
                        tasks.second.length > 0 && tasks.second.map((item, index) => (
                            <Cell
                                data-top-id={item.id}
                                onClick={(e) => this.props.go(e, { taskId : item.id })}
                                key={index}
                                title="Задание"
                                before={item.picture ? <Avatar src={item.picture}/> : <Avatar src='https://vk.com/images/camera_100.png'/>}
                            >
                                {item.name ? item.name : 'Unsigned'}
                            </Cell>
                            ))
                    }
                    </Group>
                    </List>
                </Panel>
        );
    }
}

export default Tasks;